import {TextInput} from "./text-input";
import {InputAddon} from "./input-addon";

/**
 * @classdesc Componente utilizado para renderizar input estilizado com icone monet√°rio.
 * 
 * @example 
 * 
 * import {InputAddon} from "mvcomponents/input";
 *
 * let moneyInput:MoneyInput = new MoneyInput("5,42");
 * 
 * this.append(moneyInput);
 * 
 * @export
 * @extends {TextInput}
 * @param {string} [p_text=null] Altera o texto do input.
 * @memberof module:mvcomponents/input
 */
export class MoneyInput extends TextInput {
	constructor(p_text: string = "") {
		super(p_text);
		this.setSize(3)
			.setStyleName("InputMoney")
			.append(new InputAddon()
				.setText("$"));
	}
}
